.contact {
    position: relative;
    min-height: 100vh;
    padding: 50px 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: url(../img/3.jpg);
    background-size: cover;
}

.contact .content_contact {
    max-width: 800px;
    text-align: center;
    font-weight: bold;
}

.contact .content_contact h2 {
    font-size: 36px;
    font-weight: 1000;
    color: black;
}

.contact .content_contact p {
    color: black;
    font-weight: 1000px;
}

.container_contact {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.container_contact .contactInfo {
    width: 50%;
    display: flex;
    flex-direction: column;
    padding-left: 70px;
    border-radius: 15px;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
    font-weight: bold;
}

.container_contact .contactInfo .box_contac {
    position: relative;
    padding: 20px 0;
    display: flex;
}

.container_contact .contactInfo .box_contac .icon_contact {
    min-width: 60px;
    height: 60px;
    background: coral;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 22px
}

.container_contact .contactInfo .box_contac .text_contact {
    display: flex;
    margin-left: 20px;
    font-size: 16px;
    color: black;
    font-weight: 500;
    flex-direction: column;
    font-weight: 300;
}

.container_contact .contactInfo .box_contac .text_contact h3 {
    font-weight: 500;
    color: #00bcd4;
}

.contactForm {
    width: 40%;
    padding: 40px;
    background: #fff;
}

.contactForm h2 {
    font-size: 30px;
    color: black;
    font-weight: 500;
    font-weight: 500;
}

.contactForm .inputBox {
    position: relative;
    width: 100%;
    margin-top: 10px;
}

.contactForm .inputBox input,
.contactForm .inputBox textarea {
    width: 100%;
    padding: 5px 0;
    font-size: 16px;
    margin: 10px 0;
    border: none;
    border-bottom: 2px solid #333;
    outline: none;
    resize: none;
}

.contactForm .inputBox span {
    position: absolute;
    left: 0;
    padding: 5px 0;
    font-size: 16px;
    margin: 10px 0;
    pointer-events: none;
    transition: 0.9s;
    color: #666;
}

.contactForm .inputBox input:focus~span,
.contactForm .inputBox input:valid~span,
.contactForm .inputBox textarea:focus~span,
.contactForm .inputBox textarea:valid~span {
    color: #e91e63;
    font-size: 12px;
    transform: translateY(-20px);
}

.contactForm .inputBox input[type="submit"] {
    width: 100px;
    background: #00bcd4;
    color: #fff;
    border: none;
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
}

@media (max-width: 991px) {
    .contact {
        padding: 50px;
    }
    .container_contact {
        flex-direction: column;
    }
    .container_contact .contactInfo {
        padding-left: 110px;
        margin-bottom: 40px;
    }
    .container_contact .contactInfo,
    .contactForm {
        width: 100%;
    }
}